    canUseTTS() {
        const limit = subscription_1.SUBSCRIPTION_LIMITS[this.currentSubscription.tier].ttsCharacters;
        const currentUsage = this.usageMetrics.ttsCharacters;
        // Free users can't use TTS at all
        if (limit === 0) {
            return false;
        }
        // Unlimited plan
        if (limit === -1) {
            return true;
        }
        // Check if within limits
        return currentUsage < limit;
    }
