    // Enhanced upgrade prompt with smart timing and personalization
    showSmartUpgradePrompt(feature) {
        const now = new Date();
        const nowTs = now.getTime();
        const lastPrompt = this.usageMetrics.lastUpgradePrompt;
        // Normalize last prompt to timestamp
        let lastTs = null;
        if (lastPrompt instanceof Date) {
            lastTs = lastPrompt.getTime();
        }
        else if (typeof lastPrompt === 'string') {
            const parsed = Date.parse(lastPrompt);
            lastTs = Number.isNaN(parsed) ? null : parsed;
        }
        else if (typeof lastPrompt === 'number') {
            lastTs = lastPrompt;
        }
        // Don't show too frequently (max once per hour)
        if (lastTs !== null && (nowTs - lastTs) < 60 * 60 * 1000) {
            return;
        }
        // Don't show too many times (max 3 times per day)
        if (this.usageMetrics.upgradePromptCount >= 3) {
            return;
        }
        // Store as ISO string to be JSON-friendly across sessions
        this.usageMetrics.lastUpgradePrompt = now.toISOString();
        this.usageMetrics.upgradePromptCount++;
        this.saveToStorage();
        // Create personalized upgrade message
        const trigger = this.upgradeTriggers.find(t => t.feature === feature);
        const mostUsedFeature = this.getMostUsedFeature();
        let message = `You're approaching your ${feature} limit! `;
        if (trigger) {
            const percentage = Math.round(trigger.percentage);
            message += `You've used ${percentage}% of your ${feature} allocation. `;
        }
        if (mostUsedFeature && mostUsedFeature !== feature) {
            message += `Based on your usage of ${mostUsedFeature}, you might also benefit from upgrading. `;
        }
        message += "Upgrade now to unlock unlimited access and save time on automation!";
        // Show upgrade prompt (this would integrate with your UI)
        console.log(' Upgrade Prompt:', message);
        // In a real implementation, this would trigger a UI component
        // this.showUpgradeModal(message);
    }
    showUpgradePrompt(feature) {
        if (typeof window !== 'undefined') {
            try {
                window.dispatchEvent(new CustomEvent('navigateToSection', { detail: { section: 'subscription' } }));
            }
            catch (error) {
                console.warn('[SubscriptionService] Unable to navigate to subscription sidebar:', error);
            }
        }
        this.showSmartUpgradePrompt(feature);
    }
