    // Analytics methods for business insights
    getUsageAnalytics() {
        const totalUsage = Object.values(this.usageMetrics.featureUsage).reduce((a, b) => a + b, 0);
        const mostUsedFeatures = Object.entries(this.usageMetrics.featureUsage)
            .sort(([, a], [, b]) => b - a)
            .slice(0, 3);
        return {
            totalFeatureUsage: totalUsage,
            mostUsedFeatures,
            upgradePromptCount: this.usageMetrics.upgradePromptCount,
            isNearLimits: this.isNearAnyLimit(),
            sessionTime: this.usageMetrics.sessionTime
        };
    }
