    // Method to clear localStorage for testing
    clearStorage() {
        // Clear all subscription-related localStorage items
        localStorage.removeItem('user_subscription');
        localStorage.removeItem('usage_metrics');
        localStorage.removeItem('subscription_data');
        localStorage.removeItem('current_subscription');
        localStorage.removeItem('subscription_tier');
        localStorage.removeItem('subscription_status');
        localStorage.removeItem('exclusive_games');
        // Also clear any other potential subscription-related keys
        const keysToRemove = [];
        for (let i = 0; i < localStorage.length; i++) {
            const key = localStorage.key(i);
            if (key && (key.includes('subscription') || key.includes('usage') || key.includes('tier') || key.includes('limit') || key.includes('exclusive'))) {
                keysToRemove.push(key);
            }
        }
        keysToRemove.forEach(key => {
            localStorage.removeItem(key);
            console.log(` Removed: ${key}`);
        });
        console.log(' Cleared subscription storage');
        // Force reset to free tier with correct limits
        this.forceResetToFree();
    }
