// Helper functions to extract game-specific types
function extractZombieType(effectName) {
    const zombieTypes = ['basic', 'cone', 'bucket', 'flag', 'football', 'dancing', 'gargantuar', 'imp', 'balloon', 'pogo'];
    for (const type of zombieTypes) {
        if (effectName.includes(type)) {
            return type;
        }
    }
    return 'basic';
}
